@import url(https://fonts.googleapis.com/css?family=Montserrat:400);
.Counter {
  border: 2px solid black;
  width: 200px;
  margin: 1rem auto;
  text-align: center;
}

.Counter button {
  font-size: 2rem;
}

.MoodToggler {
  font-size: 5rem;
  text-align: center;
  padding: 1rem;
}

.CartItem {
  margin-top: 2em;
  margin-bottom: 2em;
  /* display: flex;
  justify-content: space-between; */
}

.CartItem div {
  width: 25%;
}

.CartItem .qty {
  font-size: 1.0rem;
  font-weight: 200;
}

.CartItem button {
  width: 30px;
  height: 30px;
  background-color: #e1e8ee;
  border-radius: 6px;
  margin: 0 7px;
  border: none;
}

.CartItem button:disabled {
  background-color: #f8f9fa;
}

.Cart {
  width: 750px;
  margin: 80px auto;
  background: white;
  border-radius: 7px;
  box-shadow: 1px 2px 3px 0px rgba(0, 0, 0, 0.2);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 1em;
}

.Cart-title {
  border-bottom: 1px solid grey;
  color: grey;
  font-size: 1.5em;
  font-weight: 400;
  padding-bottom: 1em;
}

.Cart-total {
  color: grey;
  font-size: 1.2em;
  font-weight: 200;
}

body {
  margin: 0;
  font-family: "Montserrat";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #80def7;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

